*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: sans-serif;
  background: rgba(10,10,10,0.8);
  color: #e3e3e3;
}

.app{
 background: mediumpurple;
  background-size: cover;
  background-position: bottom;
  
  
}

.cold{
  background-image: url('./assets/img/cold-bg.jpg');
}

.warm{
  background-image: url('./assets/img/warm-bg.jpg');
  
  
}

main{
  min-height: 70vh;
  padding: 25px;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.8));
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.4);
  color: #131313;
  font-size: 24px;
  transition: 0.4s ease;
  text-align: center;
  font-weight: 600;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box .location{
  color: #fff;
  font-size: 60px;
  font-weight: 700;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);

}

.location-box .date{
  color: #fff;
  text-shadow:2px 2px rgba(0,0,0,0.4);
  font-size: 28px;
  font-weight: 600;
  font-style: italic;
  text-align: center;
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 100px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.4);
}

.range-temp{
  font-size: 30px;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.weather-box .weather{
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
}

.message{
  color: #fff;
  text-align: center;
  font-size: 50px;
  font-weight: 700;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
}

.line{
  border: #e3e3e3 1px solid;
  width: 100%;
  box-shadow: 1px 1px rgba(50,50,70,0.5);
}

.days-list{
  padding: 20px;
  display: flex;
  justify-content: space-around;
}

.days-item{
  background-color: rgba(50,50,70,0.5);
  border:none;
  font-size: 1.5rem;
  color: #e3e3e3;
  width: 100px;
  height: 100px;
  border-radius: 20%;
  cursor: pointer;
}
.days-item:hover{
  color: #131313;
  border: #131313 1px solid;
  font-size: 1.8rem;
}

.days-item .selected{
  color: #131313;
  border: #131313 1px solid;
  font-size: 1.8rem;
}